}
@media screen and (max-width: 1024px) {
  html {
    font-size: 93.75%;
  }
}
*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}
