/*

===========================================================================
Base: Utilities
===========================================================================

Version:            3.0.2

//                  3.0.2 Clearfix flexbox properties

*/
.cf {
  *zoom: 1;
}
.cf:before,
.cf:after {
  content: "";
  display: table;
  flex-basis: 0;
  order: 1;
}
.cf:after {
  clear: both;
}
.hidden-visually,
.hide-v {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.hidden-visually.focusable:active,
.hide-v.focusable:active,
.hidden-visually.focusable:focus,
.hide-v.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}
.hide-text {
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  display: block;
}
.unhide-text {
  text-indent: 0;
  white-space: normal;
}
.hidden-abs {
  position: absolute;
  left: -10000px;
  top: -10000px;
}
.hidden-abs-no {
  position: static;
  left: auto;
  top: auto;
}
.no-select {
  user-select: none;
  pointer-events: none;
  cursor: pointer;
}
.no-scroll-y {
  overflow-y: hidden;
}
.no-scroll-x {
  overflow-x: hidden;
}
/*

===========================================================================
Base: Print
===========================================================================

Version:            3.0.0

*/
@media print {
  .no-print {
    display: none;
  }
  * {
    background: transparent !important;
    color: black !important;
    box-shadow: none !important;
    text-shadow: none !important;
    filter: none !important;
    -ms-filter: none !important;
  }
  /* Black prints faster: h5bp.com/s */
  a,
  a:visited {
    text-decoration: underline;
  }
  /*a[href]:after {
      content: " (" attr(href) ")";
    }

    abbr[title]:after {
      content: " (" attr(title) ")";
    }

    .ir a:after, a[href^="javascript:"]:after, a[href^="#"]:after {
      content: "";
    }*/
  /* Don't show links for images, or javascript/internal links */
  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  /*thead {
    display: table-header-group;
  }*/
  /* h5bp.com/t */
  tr,
  img {
    page-break-inside: avoid;
  }
  #print-btn {
    display: none;
  }
  img {
    max-width: 100% !important;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  h2,
  h3 {
    page-break-after: avoid;
  }
}
